//----------  header
//------------------------------------------------------------------------------
.header {
	padding-top: 15px;
	display: flex;
}

.header__container {
	justify-content: space-between;
}

.header__logo {
	width: 150px;
	height: 80px;

	@include media-breakpoint-down(sm) {
		margin-bottom: 20px;
	}
}

.nav {
	width: 100%;
	float: left;
}

.nav__list {
	display: flex;
	justify-content: flex-end;
	float: right;
	background-color: $white;

	@include media-breakpoint-down(sm) {
		display: none;
		position: relative;
		width: 100%;
		bottom: 36px;
	}
}

.nav__item {
	border: 1px solid $gray;
	line-height: 40px;

	&:first-child {
		border-radius: 10px 0px 0px;

		@include media-breakpoint-down(sm) {
			border-radius: unset;
		}
	}

	@include media-breakpoint-down(sm) {
		margin-left: unset;
		margin-bottom: -1px;
	}

	margin-left: -1px;
}

.nav__link {
	padding: 0 20px;
	color: inherit;
	display: block;
}

.user-panel {
	display: inline-flex;
	height: 15pt;
	float: right;
}

.user-panel__item {
	margin-right: 10px;
	padding-right: 10px;
	border-right: 2px solid $gray;

	&:last-child {
		border: none;
		margin-right: unset;
		padding-right: unset;
	}
}

.user-panel__link {
	color: $black;
}

.user-panel__submenu {
	display: none;
	position: absolute;
	margin-left: 10px;
	padding: 10px;
	z-index: 9999;
	border: 1px solid $gray;
	background-color: $white;
}

.user-panel__item:hover .user-panel__submenu {
	display: block;
}

.user-panel__submenu:hover {
	display: block;
}

.nav__mobile {
	display: none;

	@include media-breakpoint-down(sm) {
		position: relative;
		display: block;
		float: right;
		bottom: 68px;
		margin-right: 10px;
		font-size: 24pt;
	}
}
