//----------  layout
//------------------------------------------------------------------------------

html, body {
	height: 100%;
}

body {
	font-family: $font-default;
	font-size: 15px;
	color: $black;
	height:100%;
}

a {
	color: $black;
}

.wrap {
	margin: 0 auto;
	max-width: 1440px;
	padding: 0 30px;
	width: 100%;

	@include media-breakpoint-down(sm) {
		padding: 0 8px;
	}
}

.layout__wrapper {
	min-height: calc(100vh - 50px);

	@include media-breakpoint-down(sm) {
		min-height: calc(100vh);
	}
}

.content {
	margin-top: -1px;
	padding: 5px 10px 40px 10px;
	background: $white;
	border: solid 1px $gray;
	margin-bottom: 20px;
	width: 100%;

	@include media-breakpoint-down(sm) {
		border: unset;
		padding: unset;
	}
}

.content__heading {
	border-bottom: solid 1px $gray;
	width: 100%;

	@include media-breakpoint-down(sm) {
		h2 {
			text-align: center;
		}

		border: unset;
	}
}

.content__section{
	padding-top:30px;
}

.content__footer {
	height: 40px;
	width: 100%;
	border: 1px solid #cccbcb;
	text-align: right;
	padding-right: 20px;
	line-height: 40px;
	font-weight: 700;
	margin-top: -1px;

	b {
		color: $orange;
	}
}

.active {
	background-color: $orange;
	color: $white;
	border: 1px solid $darkOrange !important;
	z-index: 1 !important;

	@include hover {
		background-color: $darkOrange;
		transition: 0.4s ease-out;
	}

	a {
		color: $white !important;
	}
}

.disabled {
	background: #e6e6e6;
	color: #acacac;
	pointer-events: none;
}

.time-picker {
	cursor: pointer;
}

.centered {
	text-align: center;
}

.float-right{
	float:right;
}
