//---------- error-page
//------------------------------------------------------------------------------

.moving-zone {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 300px;
	height: 120px;
	margin: -60px 0 0 -150px;
	perspective: 400px;
}

.popup {
	position: absolute;
	width: 300px;
	padding: 10px;
	box-sizing: border-box;
	border-radius: 20px 0 20px 0;
	cursor: pointer;
	transform-style: preserve-3d;
	background: -webkit-linear-gradient(top left, $black 50%, $orange 50%);
	background: -moz-linear-gradient(top left, $black 50%, $orange 50%);
	background: -ms-linear-gradient(top left, $black 50%, $orange 50%);
	background: -o-linear-gradient(top left, $black 50%, $orange 50%);
	background: linear-gradient(top left, $black 50%, $orange 50%);
}

.popup:before {
	content: '';
	position: absolute;
	left: 5%;
	top: 5%;
	width: 90%;
	height: 90%;
	border-radius: inherit;
	background: rgba(0,0,0,.1);
	box-shadow: 0 0 40px 20px rgba(0,0,0,.1);
	transform: translateZ(-100px);
}

.popup-content {
	background: $white;
	padding: 20px;
	box-sizing: border-box;
	border-radius: 10px 0 10px 0;
}

.popup-text {
	text-align: center;
	transform: translateZ(15px);
}

.popup-text b {
	color: $orange;
	font-weight: 300;
}

.background__canvas {
	width: 100%;
	height: 100%;
	position: absolute;
	left: 0px;
	top: 0px;
}

.error__page {
	background: #272727;
}
