//----------  forms
//------------------------------------------------------------------------------
input,
textarea {
	height: 40px;
	border: 1px solid $gray;
	font-family: $font-default;
	padding: 0 10px;
	box-shadow:none;
}

.standard-form {
	text-align: center;
	margin: 0 auto;
	width: 100%;

	&.md {
		width: 33%;

		@include media-breakpoint-down(sm) {
			width: 100%;
		}
	}

	input,
	textarea,
	select {
		border-radius: 5px;
		width: 100%;
	}

	textarea {
		height: 300px;
	}

	li {
		padding-top: 30px;
	}

	label {
		display: block;
		padding-bottom: 10px;
		text-transform: uppercase;
		font-weight: bold;
	}
}
