//---------- home
//------------------------------------------------------------------------------

.board {
	margin-top: 15px;
	display: flex
}

.board__month-table {
	width: calc(100% - 40px);
	table-layout: fixed;

	@include media-breakpoint-down(sm) {
		width: calc(100% - 31px);
	}

	tbody:before {
		line-height: 10px;
		content: "_";
		color: white;
		display: block;

		@include media-breakpoint-down(sm) {
			content: unset;
		}
	}

	.date {
		font-weight: 700;
	}

	td {
		position: relative;
		border: 1px solid $gray;
		padding: 5px;
		cursor: pointer;

		&:hover .total {
			color: $orange;
			transition: 0.4s ease-out;
		}
	}

	tbody tr {
		height: 100px;
		overflow: hidden;

		@include media-breakpoint-down(sm) {
			height: unset;
			text-align: center;
		}
	}

	.head {
		text-transform: uppercase;

		@include media-breakpoint-down(sm) {
			display: none;
		}
	}

	.mobile-head {
		display: none;

		@include media-breakpoint-down(sm) {
			display: table-row;
			height: 31px;
		}
	}

	.orders {
		padding-bottom: 40px;

		@include media-breakpoint-down(sm) {
			display: none;
		}
	}

	.total {
		position: absolute;
		bottom: 0px;
		right: 0px;
		margin: 5px;
		color: inherit;

		@include media-breakpoint-down(sm) {
			display: none;
		}
	}

	.mobile-head th {
		border: 1px solid $gray;
	}
}

.board__month-navigation {
	position: relative;
	top: 29px;
	background-color: $black;
	display: flex;
	text-orientation: mixed;
	writing-mode: vertical-lr;
	-webkit-transform: rotate(180deg);
	overflow: hidden;

	@include media-breakpoint-down(sm) {
		top: unset;
	}

	li:first-child, li:last-child {
		position: relative;
		font-size: 17px;
		background-color: $black;
		z-index: 99;
	}

	li:first-child {
		padding: 10px 10px 20px 10px;
	}


	li {
		padding: 20px 10px 10px 10px;
		font-weight: 700;
		color: $white;
		cursor: pointer;
		transition: 1s ease-in-out;
		text-align: center;

		@include media-breakpoint-down(sm) {
			padding: 5px !important;
			margin: auto;
			min-height: 31px;
			min-width: 31px;
			max-width: 31px;
		}

		&:hover {
			a {
				color: $orange;
				transition: 0.4s ease-out;
			}
		}

		&.active {
			border: unset !important;
			background-color: unset;
			pointer-events: none;

			a {
				color: $orange !important;
			}

			&:hover {
				background-color: unset;
			}
		}

		a {
			color: inherit;
			outline: 0;
		}
	}

	.month {
		@include media-breakpoint-down(sm) {
			display: none;
		}
	}

	.mobile-month {
		writing-mode: initial;
		-webkit-transform: rotate(180deg);
		display: none;

		@include media-breakpoint-down(sm) {
			display: block;
			text-align: center;
			width: 100%;
		}
	}
}
