//---------- login
//------------------------------------------------------------------------------

.login {
	position: relative;
	top: 25%;
}

.login__box {
	max-width: 400px;
	width: 80%;
	margin: 0 auto;
	background-color: $white;
	border-radius: 30px;
	border: 1px solid $gray;
	text-align: center;
	padding: 50px 50px 45px;

	@include media-breakpoint-down(sm) {
		border: unset;
		padding: 0px 10px 0px 10px;
		width: 100%;
	}
}

.login__logo {
	margin-bottom: 50px;
}

.login__btn {
	margin-bottom: 22px;
}

.login__link {
	display: block;
	font-weight: 700;
}

.login__item {
	margin-bottom: 24px;

	input {
		height: 40px;
		line-height: 40px;
		border: 1px solid #cccbcb;
		border-radius: 5px;
		width: 100%;
		color: #444;
		font-weight: 600;

		&::placeholder {
			color: #444;
		}
	}
}
