//---------- order-meal
//------------------------------------------------------------------------------

.order__table {
	text-align: center;
	padding-top: 45px;

	@include media-breakpoint-down(sm) {
		padding-top: 10px;
	}

	h3 {
		text-transform: uppercase;
		padding-bottom: 10px;
	}

	table {
		width: 100%;

		thead > tr {
			height: 40px;
			color: $white;
			background-color: $black;
		}

		td > input, select {
			width: 100%;
			height: 40px;
			border: 1px solid $gray;
			border-right: unset;
		}
	}

	.order__week {
		display: flex;
		width: 100%;
		height: 60px;
		text-align: left;

		@include media-breakpoint-down(sm) {
			display: none;
		}

		li:first-child, li:last-child {
			max-width: 54px;
			padding-left: 13px;
			padding-right: 13px;
			font-size: 48px;
			font-weight: 700;
			line-height: 35px;
		}

		li {
			margin: 0 auto;
			width: 100%;
			border: 1px solid $gray;
			margin-left: -1px;
			padding: 10px;

			b {
				text-transform: uppercase;
			}

			&:hover {
				cursor: pointer;

				a {
					color: $orange;
					transition: 0.4s ease-out;
				}
			}
		}
	}

	.order__week-mobile {
		display: none;
		height: 37.5px;

		@include media-breakpoint-down(sm) {
			display: flex !important;
		}

		li {
			padding: unset;
			display: flex;
			justify-content: center;
			line-height: 36px;
		}

		li:first-child, li:last-child {
			font-size: 24px;
			max-width: 20px;
			line-height: 36px;
		}
	}
}

.table {
	display: flex;
	flex-flow: column nowrap;
	flex: 1 1 auto;
}

.th {
	display: none;
	font-weight: bold;
	height: 40px;
	color: $white;

	@include media-breakpoint-down(md) {
		display: none !important;
	}
}

.th > .td {
	white-space: normal;
	justify-content: center;
	background-color: $black;
	line-height: 40px;
	font-size: 17px;
}

.tr {
	height: 40px;
	width: 100%;
	display: flex;
	flex-flow: row nowrap;

	@include media-breakpoint-down(md) {
		display: inline-table;
		flex-flow: unset;

		.td {
			display: none;
		}
	}
}

.td {
	display: flex;
	flex-flow: row nowrap;
	flex-grow: 1;
	flex-basis: 0;
	word-break: break-word;
	overflow: hidden;
	text-overflow: ellipsis;
	min-width: 0px;
	white-space: nowrap;

	&.accordion {
		display: none;
	}

	@include media-breakpoint-down(md) {
		&.accordion {
			display: block!important;
		}
	}
}

.td > input, select, .select-style {
	width: 100%;
	height: 40px;
	border: 1px solid $gray;
	border-right: unset !important;
	font-family: $font-default;

	@include media-breakpoint-down(md) {
		border: 1px solid $gray !important;
		border-bottom: unset !important;
	}
}

.ui-corner-all {
	font-family: $font-default;
}

.order__table-operations {
	display: flex;
	border: 1px solid $gray;
	width: 100%;

	.save, .delete {
		color: white;
		width: 50%;
	}
}

.accordion {
	color: $white;
	background-color: $black;
	line-height: 40px;
	font-size: 17px;
	text-align: left;
	padding-left: 12px;

	&::after{
		content: '+';
		float:right;
		margin-right: 10px;
		transition: linear 1s;
	}

	&.open::after{
		content: '-';
	}
}
