//----------  variables
//------------------------------------------------------------------------------

$font-default: 'Ubuntu';

// Colors
$white: #fff;
$black: #444444;
$orange: #ffaf46;
$gray: #cccbcb;
$darkOrange: #e59c3d;
$green: #8dc63f;
$red: #ed1c24;

$site-colors: ( 'black': $black, 'white': $white, 'orange': $orange, 'dark-orange': $darkOrange, 'gray': $gray ) !default;

//----------  Bootstrap grid
//------------------------------------------------------------------------------

$enable-grid-classes: true !default;

//----------  Media queries breakpoints - Grid breakpoint
//------------------------------------------------------------------------------

$grid-breakpoints: ( xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px ) !default;

//----------  Grid containers
//------------------------------------------------------------------------------

$container-max-widths: ( sm: 540px, md: 720px, lg: 960px, xl: 1140px ) !default;

//----------  Grid columns
//------------------------------------------------------------------------------

$grid-columns: 12 !default;
$grid-gutter-width: 30px !default;
